import React, { useState, useEffect } from 'react'

const OpencvTest = () => {
  const [imageLoaded, setImageLoaded] = useState(false)
  const [opencvLoaded, setOpencvLoaded] = useState(false)
  const [selectedImage, setSelectedImage] = useState(null)

  useEffect(() => {
    const loadImage = async () => {
      if (opencvLoaded && selectedImage) {
        const imgElement = document.getElementById('opencv-image')
        try {
          // Ensure cv is defined before using it
          if (window.cv && window.cv.Mat && imgElement) {
            const src = window.cv.imread(imgElement)

            // Perform OpenCV operations

            // Release resources
            src.delete()

            // Set the flag to indicate that the image is loaded
            setImageLoaded(true)
          }
        } catch (error) {
          console.error('Error processing image with OpenCV:', error)
        }
      }
    }

    loadImage()
  }, [opencvLoaded, selectedImage])

  const handleOpenCvLoad = () => {
    // Load OpenCV when the button is clicked
    const script = document.createElement('script')
    script.src = 'https://docs.opencv.org/master/opencv.js'
    script.onload = () => {
      // OpenCV is now loaded
      //setOpencvLoaded(true)
      console.log('ladattu')
    }
    document.head.appendChild(script)
  }

  const handleImageChange = (event) => {
    const file = event.target.files[0]
    if (file) {
      setSelectedImage(URL.createObjectURL(file))
      setImageLoaded(false)
    }
  }

  return (
    <div>
      <h1>Gatsby with OpenCV Example</h1>
      <button onClick={handleOpenCvLoad}>Load OpenCV</button>
      <input type="file" accept="image/*" onChange={handleImageChange} />
      {selectedImage && (
        <img
          id="opencv-image"
          alt="Example Image"
          src={selectedImage}
          onLoad={() => {
            if (!imageLoaded) {
              setImageLoaded(true)
            }
          }}
        />
      )}
    </div>
  )
}

export default OpencvTest
